<template>
<div id="userWindows">
    <el-table :data="data" border style="width:79.4%" v-loading="loading" height="350">
        <!-- 窗口号 -->
        <el-table-column prop="windowNo" label="窗口号" width="180"></el-table-column> 
        <!-- 工作人员姓名 -->
        <el-table-column prop="staffName" label="工作人员姓名" width="180"></el-table-column>
        <!-- 窗口状态 -->
        <el-table-column prop="windowStatusName" label="窗口状态" width="180"></el-table-column>
        <!-- 已完成 -->
        <el-table-column prop="statusName" label="窗口当前工作状态" width="180">
            <template slot-scope="scope">
                <el-tag :type="windowStatus(scope.row)" >{{scope.row.statusName}}</el-tag>
            </template>
        </el-table-column>
        <!-- 最大处理 -->
        <el-table-column prop="businessName" label="当前办理业务" width="180"></el-table-column>
        <el-table-column prop="waitNum" label="当前办理业务的等候人数" width="220"></el-table-column>
    </el-table>
    <el-dialog title="业务所需材料" :visible.sync = 'outerVisible' width="35%" >
        <el-form :model="materials" :ref="materials"  v-for="materials in materialses">
            <el-form-item>
                <el-input disabled v-model="materials.materialsName"></el-input>
            </el-form-item>
        </el-form>
        <el-dialog width="30%" title="选择预约时间" :visible.sync="innerVisible" append-to-body>
            <div class="block">
                <el-date-picker v-model="appointmentMes.handler" type="date" placeholder="选择日期时间"></el-date-picker>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="closeAll">取消</el-button>
                <el-button @click="commit" type="primary">确定预约</el-button>
            </div>
        </el-dialog>
        <div slot="footer" class="dialog-footer">
            <el-button @click="outerVisible=false">取消</el-button>
            <el-button type="primary" @click="first">选择预约时间</el-button>
        </div>
    </el-dialog>
</div>
</template>
<script>
export default {
    data(){
        return{
            data:[],
            materialses:[],
            loading:true,
            materialsloading:true,
            appointmentMes:{businessId:'',windowId:'',userId:'',handler:''},
            outerVisible:false,
            innerVisible:false,
        }
    },
    mounted(){
        this.init()
    },
    methods:{
        init(){
            this.axios.getWindows({}).then((res)=>{
                this.loading = false
                this.data = res.dataList
            })
        },
        checkAppoint(row){
            if(row.windowStatus == 1 || row.status == 0){
                return true
            }
            return false
        },
        appointment(row){
            this.outerVisible=true
            this.appointmentMes.windowId = row.id  //窗口ID
            this.appointmentMes.businessId = row.businessId //业务ID
            this.axios.getmaterials({'businessId':row.businessId}).then((res)=>{
                this.materialsloading = false
                this.materialses = res.dataList
            })
        },
        first(){
            this.appointmentMes.handler = ''
            this.innerVisible = true
            this.appointmentMes.userId = sessionStorage.getItem('id') //用户ID
        },
        closeAll(){
            this.outerVisible = false
            this.innerVisible = false
        },
        commit(){
            let year = this.appointmentMes.handler.getFullYear()
            let month = this.appointmentMes.handler.getMonth()+1
            let day = this.appointmentMes.handler.getDate()
            this.appointmentMes.handler = String(year+"-"+month+"-"+day)
            this.axios.subscribe(this.appointmentMes).then((res)=>{
                if(res.success){
                    this.$message.success("预约业务成功")
                    this.closeAll()
                }
            })
        },
        windowStatus(row){
            let type = row.status //当前窗口状态
            if(type == 1){
                return 'success'
            }else
                return 'danger'
        }
    }
}
</script>
<style lang="scss">
#userWindows{
    .add{
        position: relative;
        top: 100px;
        left: 365px;
    }
    .el-table{
        border:1px solid #000;
        border-radius: 7px 7px 7px 7px;
        box-shadow:0px 0px 3px #000;
        position: relative;
        top:100px;
        left: 150px;
    }
}
</style>


